<template>
    
  <form @submit.prevent="save">

    <div class="form-group">
        <label>Clave del concepto</label>
        <input type="text" v-model="model.concepto" class="form-control" placeholder="Escribe el nombre de la columna"/>
    </div>
    <div class="form-group">
        <label>¿A que costo está relacionado?</label>
      <select class="form-control" v-model="model.tipo">
          <option value="envio">Costo de amazon</option>
          <option value="producto">Costo del producto</option>
      
      </select>
    </div>


    <div class="form-group">
        <label><input type="checkbox" true-value="1" false-value="0" v-model.number="model.activo"/> Activo</label>
    </div>


    <input type="submit" rounded class="btn btn-info" value="Guardar"/>
    
    <router-link class="btn btn-clean" :to="{path:'/conceptoimportes'}">Cancelar</router-link>

  </form>



</template>


<script>


import api from '@/util/api.js';



export default{

    name:"formConceptoimporte",
    components:{
        


    },

    props:{

      id:{
        type:Number,
        required:false,
      }
    },
    data(){

      return{

        isNewRecord:true,
        model:{concepto:'',tipo:'',activo:1}

      }

      },


    methods:{

      save(){

        
              api.request(
              {url:this.isNewRecord?'/conceptoimportes':'/conceptoimportes/'+this.$props.id,
              data:this.createFormData(this.model),
              method:this.isNewRecord?'POST':'PATCH',
              }).then(()=>{


                  this.$router.push('/conceptoimportes');
                  

              }).catch(error=>{

                console.log(error);

              });


              


      }

    },

    mounted()
    {



      const id=this.$props.id;
      if(id){

        this.isNewRecord=false;


        api.get('/conceptoimportes/'+id,this.createFormData(this.model)).then(response=>{

          this.fillModel(this.model,response.data);



          }).catch(error=>{

          console.log(error);

          });

      }


    }


}

</script>